	module.exports = {"data":{"markdownRemark":{"html":"<p>英語と哲学の授業だったかな？大学の授業初日のノート。最初からダメダメ路線を邁進していたと言う、揺るぎない証拠である。 この頃キャラの脚を短く描いてしまう癖があり、描き途中に気づいて伸ばそうとして、足の先が紙よりはみ出てしまうことが多々あった。やっぱデッサンは大事なんだね。 (と言いつつも、2004年以降生身の人間をデッサンしたことが一度もない、と言うより出来る機会がないorz)</p>","frontmatter":{"cover":null,"date":"2000-04-10T15:00:00+09:00","strDate":"2000/4/10","title":"最初の授業"}}},"pathContext":{"path":"/ja/blog/2000/04/10-first-day-of-univ/","langKey":"ja"}}

